import React from 'react'
import FilmList from '../lists/filmList';
import PlanetList from '../lists/planetList'
import StarShipList from '../lists/starShipList'
import VehiclesList from '../lists/vehiclesList'
import SpeciesList from '../lists/speciesList'
import {withRouter} from 'react-router-dom'

const Character = (props) => {
    return (
        <div className="page">
            <button className="goBack" onClick={props.history.goBack}>Go back</button>
            <div className="info">
                <img alt={props.info.name} key={props.info.name} src={`https://starwars-visualguide.com/assets/img/characters/${props.info.url.split("/")[5]}.jpg`} />
                <div className="data">
                    <h2>Name : {props.info.name}</h2>
                    <p>Height: {props.info.height}</p>
                    <p>Mass: {props.info.mass}</p>
                    <p>Hair color: {props.info.hair_color}</p>
                    <p>Skin color: {props.info.skin_color}</p>
                    <p>Birth year: {props.info.birth_year}</p>
                    <p>Gender: {props.info.gender}</p>
                    <p>Homeworld: </p>
                    <PlanetList planet={props.info.homeworld} />
                </div>
            </div>
            <div className="related">
                <h3>Appears in</h3>
                <div className="minis">
                    {props.info.films.map(film => {
                        return (
                            <FilmList key={`film-${film.split("/")[5]}`} film={film} />
                        )
                    })}
                </div>
                <h3>StarShips</h3>
                <div className="minis">
                    {props.info.starships.map(ship => {
                        return (
                            <StarShipList key={`spaceship-${ship.split("/")[5]}`} starShip={ship} />
                        )
                    })}
                </div>
                <h3>Vehicles</h3>
                <div className="minis">
                    {props.info.vehicles.map(vehicle => {
                        return (
                            <VehiclesList key={`vehicle-${vehicle.split("/")[5]}`} vehicles={vehicle} />
                        )
                    })}
                </div>
                <h3>Specie</h3>
                <div className="minis">
                    {props.info.species.map(specie => {
                        return (
                            <SpeciesList key={`specie-${specie.split("/")[5]}`} species={specie} />
                        )
                    })}
                </div>
            </div>
        </div>
    )
}
export default withRouter(Character)
