import React, { useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import PreviewFilms from './previewFilms'
import Film from './film'

const Films = (props) => {
    const [films, setFilms] = useState({ "results": [] });
    useEffect(() => {
        const getFilms = async () => {
            if (localStorage.getItem('filmStored')===null) {
                var url = `https://swapi.co/api/films/`
                const resp = await fetch(url);
                const data = await resp.json();
                setFilms(data);
                localStorage.setItem('filmStored', JSON.stringify(data));
            } else {
                setFilms(JSON.parse(localStorage.getItem('filmStored')));
            }
        }
        getFilms();
    }, []);

    let filme = films.results.map((thisFilm, i) => {
        return (
            <Route path={`/Films/${thisFilm.title}`} key={`${thisFilm.title}-Page`} render={() => <Film info={thisFilm} i={i} />} />
        )
    });


    return (
        <div className="Wrapper">
            <Route exact path="/Films">
                <div className="films">
                    <PreviewFilms films={films.results} />
                </div>
            </Route>
            {filme}
        </div>
    )
}

export default Films
