import React from 'react'
import CharactersList from '../lists/characterList'
import FilmsList from '../lists/filmList'
import { withRouter } from 'react-router-dom'

const Planet = (props) => {
    return (
        <div className="filmPage">
            <button className="goBack" onClick={props.history.goBack}>Go back</button>
            <div className="info">
                <img
                    alt={props.info.name}
                    src={`https://starwars-visualguide.com/assets/img/planets/${props.info.url.split("/")[5]}.jpg`} 
                    onError={(e)=>{e.target.onerror = null; e.target.src="https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg"}}
                    />
                <div className="data">
                    <h2>{props.info.name}</h2>
                    <p>{`Rotation period : ${props.info.rotation_period}`}</p>
                    <p>{`Orbital period : ${props.info.orbital_period}`}</p>
                    <p>{`Diameter : ${props.info.diameter}`}</p>
                    <p>{`Climate : ${props.info.climate}`}</p>
                    <p>{`Gravity : ${props.info.gravity}`}</p>
                    <p>{`Terrain : ${props.info.terrain}`}</p>
                    <p>{`Surface water : ${props.info.surface_water}`}</p>
                    <p>{`Population : ${props.info.population}`}</p>
                </div>
            </div>
            <div className="related">
                <h1>Residents</h1>
                <div className="Characters minis">
                    {props.info.residents.map(character => (<CharactersList key={`planet-character-${character.split("/")[5]}`} character={character} />))}
                </div>
                <h1>Appears in</h1>
                <div className="filmsMini minis">
                    {props.info.films.map(film => (<FilmsList key={`planet-film-${film.split("/")[5]}`} film={film} />))}
                </div>
            </div>
        </div>
    )
}

export default withRouter(Planet)
