import React, { useEffect, useState } from 'react'
import PreviewStarShips from './previewStarShips'
import { Route } from 'react-router-dom'
import StarShip from "./starShip"

const StarShips = () => {
    const [StarShips, setStarShips] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getStarShips = async () => {
            if (localStorage.getItem('StarShipStored') === null) {
                let allData = [];
                for (let i = 1; i <= 4; i++) {
                    let url = `https://swapi.co/api/starships?page=${i}`
                    const resp = await fetch(url);
                    const data = await resp.json();
                    allData = allData.concat(data.results);
                    setStarShips(allData);
                }
                localStorage.setItem('StarShipStored', JSON.stringify(allData));
            } else {
                console.log("cached")
                setStarShips(JSON.parse(localStorage.getItem('StarShipStored')));
            }
        }
        getStarShips();
    }, []);

    let starShip = StarShips.map((thisStarShip) => {
        return (
            <Route path={`/Spaceships/${thisStarShip.name}`} key={`${thisStarShip.name}-Page`} render={() => <StarShip info={thisStarShip} />} />
        )
    });


    const nextPage = () => {
        if (pageNumber < 4) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (pageNumber > 1) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Spaceships">
                <div className="StarShips">
                    {StarShips.slice((pageNumber - 1) * 10, pageNumber * 10).map((spaceShips) => {
                        return (
                            <PreviewStarShips
                                key={spaceShips.name}
                                info={spaceShips}
                                id={spaceShips.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {starShip}
        </div>
    )
}

export default StarShips
