import React, { useEffect, useState } from 'react'
import Previewvehicles from './previewVehicles'
import { Route } from 'react-router-dom'
import Vehicle from './vehicle'

const Vehicles = () => {
    const [vehicles, setVehicles] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getVehicles = async () => {
            if (localStorage.getItem('vehicleStored') === null) {
                let allData = [];
                for (let i = 1; i <= 4; i++) {
                    let url = `https://swapi.co/api/vehicles?page=${i}`
                    const resp = await fetch(url);
                    const data = await resp.json();
                    allData = allData.concat(data.results);
                    setVehicles(allData);
                }
                localStorage.setItem('vehicleStored', JSON.stringify(allData));
            } else {
                console.log("cached")
                setVehicles(JSON.parse(localStorage.getItem('vehicleStored')));
            }
        }
        getVehicles();
    }, []);

    let vehicle = vehicles.map((thisVehicle) => {
        return (
            <Route path={`/Vehicles/${thisVehicle.name}`} key={`${thisVehicle.name}-Page`} render={() => <Vehicle info={thisVehicle} />} />
        )
    });

    const nextPage = () => {
        if (pageNumber < 4) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (pageNumber > 1) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Vehicles">
                <div className="vehicles">
                    {vehicles.slice((pageNumber - 1) * 10, pageNumber * 10).map((vehicle) => {
                        return (
                            <Previewvehicles
                                key={vehicle.name}
                                info={vehicle}
                                id={vehicle.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {vehicle}
        </div>
    )
}

export default Vehicles
