import React, { useEffect, useState } from 'react'
import Preview from './preview'
import { Route } from 'react-router-dom'
import Character from "./character"

const Characters = () => {
    const [characters, setCharacters] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getCharacters = async () => {
            if (localStorage.getItem('characterStored') === null) {
                let allData = [];
                for (let i = 1; i <= 9; i++) {
                    let url = `https://swapi.co/api/people?page=${i}`
                    const resp = await fetch(url);
                    const data = await resp.json();
                    allData = allData.concat(data.results);
                    setCharacters(allData);
                }
                localStorage.setItem('characterStored', JSON.stringify(allData));
            } else {
                console.log("cached")
                setCharacters(JSON.parse(localStorage.getItem('characterStored')));
            }
        }
        getCharacters();
    }, []);

    let character = characters.map((thisCharacter, i) => {
        return (
            <Route path={`/Characters/${thisCharacter.name}`} key={`${thisCharacter.name}-Page`} render={() => <Character info={thisCharacter} i={i} pageNumber={pageNumber} />} />
        )
    });


    const nextPage = () => {
        if (pageNumber<9) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (pageNumber>1) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Characters">
                <div className="characters">
                    {characters.slice((pageNumber - 1) * 10, pageNumber * 10).map((character) => {
                        return (
                            <Preview
                                key={character.name}
                                info={character}
                                name={character.name}
                                number={character.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {character}
        </div>
    )
}

export default Characters
