import React from 'react'
import { Link } from 'react-router-dom'

const PreviewPlanets = (props) => {
    return (
        <div className="planets Preview" key={`Planet-${props.name}`}>
            <Link to={`/Planets/${props.name}`}>
                <h3>{props.name}</h3>
                <img
                    alt={props.name}
                    src={`https://starwars-visualguide.com/assets/img/planets/${props.id}.jpg`}
                    onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                />
            </Link>
        </div>
    )
}

export default PreviewPlanets
