import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const CharacterList = (props) => {
    const [character, setCharacter] = useState({});
    useEffect(() => {
        const getCharacter = async () => {
            var url = props.character
            if (JSON.parse(localStorage.getItem('characterListStored')) === null) {
                const resp = await fetch(url);
                const data = await resp.json();
                const actaulCharacter = {
                    [props.character]: data
                };
                const allData = Object.assign(actaulCharacter, JSON.parse(localStorage.getItem('characterListStored')));
                setCharacter(data);
                localStorage.setItem('characterListStored', JSON.stringify(allData));
            } else {
                if (props.character in JSON.parse(localStorage.getItem('characterListStored')) === true) {
                    setCharacter(JSON.parse(localStorage.getItem('characterListStored'))[props.character]);
                    console.log("cached");
                } else {
                    const resp = await fetch(props.character);
                    const data = await resp.json();
                    const actaulCharacter = {
                        [props.character]: data
                    };
                    const allData = Object.assign(actaulCharacter, JSON.parse(localStorage.getItem('characterListStored')));
                    setCharacter(data);
                    localStorage.setItem('characterListStored', JSON.stringify(allData));
                }
            }
        }
        getCharacter();
    }, [props.character]);
    return (
        <div className="character">
            <Link to={`/Characters/${character.name}`}>
                <div className="mini">
                    <img
                        alt={`Mini-${character.name}`}
                        src={`https://starwars-visualguide.com/assets/img/characters/${props.character.split("/")[5]}.jpg`}
                        onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                    />
                </div>
            </Link>
        </div>
    )
}
export default CharacterList
