import React, { useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import PreviewPlanets from './previewPlanets'
import Planet from './planet'

const Planets = (props) => {
    const [planets, setPlanets] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getPlanets = async () => {
            if (localStorage.getItem('planetStored') === null) {
                let allData = [];
                for (let i = 1; i <= 7; i++) {
                    let url = `https://swapi.co/api/planets?page=${i}`
                    const resp = await fetch(url);
                    const data = await resp.json();
                    allData = allData.concat(data.results);
                    setPlanets(allData);
                }
                localStorage.setItem('planetStored', JSON.stringify(allData));
            } else {
                console.log("cached")
                setPlanets(JSON.parse(localStorage.getItem('planetStored')));
            }
        }
        getPlanets();
    }, []);

    const nextPage = () => {
        console.log(pageNumber)
        if (pageNumber < 7) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (pageNumber > 1) {
            setPageNumber(pageNumber - 1)
        }
    }

    let planeta = planets.map((thisPlanet) => {
        return (
            <Route path={`/Planets/${thisPlanet.name}`} key={`${thisPlanet.name}-Page`} render={() => <Planet info={thisPlanet} />} />
        )
    });


    return (
        <div className="Wrapper">
            <Route exact path="/Planets">
                <div className="planets">
                    {planets.slice((pageNumber - 1) * 10, pageNumber * 10).map((planet) => {
                        return (
                            <PreviewPlanets
                                key={planet.name}
                                name={planet.name}
                                id={planet.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {planeta}
        </div>
    )
}

export default Planets
