import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const SpeciesList = (props) => {
    const [species, setSpecies] = useState({ "results": [] });
    useEffect(() => {
        const getSpecies = async () => {
            var url = props.species
            if (JSON.parse(localStorage.getItem('speciesListStored')) === null) {
                const resp = await fetch(url);
                const data = await resp.json();
                const actualSpecie = {
                    [props.species]: data
                };
                const allData = Object.assign(actualSpecie, JSON.parse(localStorage.getItem('speciesListStored')));
                setSpecies(data);
                localStorage.setItem('speciesListStored', JSON.stringify(allData));
            } else {
                if (props.species in JSON.parse(localStorage.getItem('speciesListStored')) === true) {
                    setSpecies(JSON.parse(localStorage.getItem('speciesListStored'))[props.species]);
                    console.log("cached");
                } else {
                    const resp = await fetch(url);
                    const data = await resp.json();
                    const actualSpecie = {
                        [props.species]: data
                    };
                    const allData = Object.assign(actualSpecie, JSON.parse(localStorage.getItem('speciesListStored')));
                    setSpecies(data);
                    localStorage.setItem('speciesListStored', JSON.stringify(allData));
                }
            }
        }
        getSpecies();
    }, [props.species]);
    return (
        <div className="species">
            <Link to={`/Species/${species.name}`}>
                <div className="species mini">
                    <img
                        alt={species.name}
                        src={`https://starwars-visualguide.com/assets/img/species/${props.species.split("/")[5]}.jpg`}
                        onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                    />
                </div>
            </Link>
        </div>
    )
}
export default SpeciesList
