import React from 'react'
import { Link } from 'react-router-dom'

const PreviewFilms = (props) => {
    const order = [1, 5, 4, 6, 3, 2, 7];
    return (
        <React.Fragment>
            {props.films.map((film, i) => {
                return (
                    <div key={`film:${film.title}`} className="films Preview">
                        <Link to={`/Films/${film.title}`}>
                            <h3>{film.title}</h3>
                            <img
                                alt={film.title}
                                src={`https://starwars-visualguide.com/assets/img/films/${order[i]}.jpg`}
                            />
                        </Link>
                    </div>
                )
            })}
        </React.Fragment>
    )
}

export default PreviewFilms
