import React from 'react'
import { Link } from 'react-router-dom'

const PreviewSpecies = (props) => {
    return (
        <div className="species Preview">
            <Link to={`/Species/${props.info.name}`}>
                <h3>{props.info.name}</h3>
                <img
                    src={`https://starwars-visualguide.com/assets/img/species/${props.id}.jpg`}
                    alt={props.info.name + "image"}
                />
            </Link>
        </div>
    )
}

export default PreviewSpecies
