import React from 'react'
import CharactersList from '../lists/characterList'
import PlanetsList from '../lists/planetList'
import { withRouter } from 'react-router-dom'

const Film = (props) => {
    const order = [1, 5, 4, 6, 3, 2, 7];
    return (
        <div className="filmPage">
            <button className="goBack" onClick={props.history.goBack}>Go back</button>            <div className="info">
                <img
                    alt={props.info.title}
                    src={`https://starwars-visualguide.com/assets/img/films/${order[props.i]}.jpg`}
                />
                <div className="data">
                    <h2>{props.info.title}</h2>
                    <p>{`Episode : ${props.info.episode_id}`}</p>
                    <p>{`Director : ${props.info.director}`}</p>
                    <p>{`Producer : ${props.info.producer}`}</p>
                    <p>{`release date : ${props.info.release_date}`}</p>
                    <h3>Opening Crawl</h3>
                    <div className="openingCrawl">
                        <p>{props.info.opening_crawl}</p>
                    </div>
                </div>
            </div>
            <div className="related">
                <h1>Characters in Film</h1>
                <div className="CharactersMini minis">
                    {props.info.characters.map(character => (<CharactersList key={`film-character-${character.split("/")[5]}`} character={character} />))}
                </div>
                <h1>Planets in Film</h1>
                <div className="planetsMini minis">
                    {props.info.planets.map(planet => (<PlanetsList key={`film-planet-${planet.split("/")[5]}`} planet={planet} />))}
                </div>
            </div>
        </div>
    )
}

export default withRouter(Film)
