import React from 'react'
import { Link } from 'react-router-dom'

const PreviewVehicles = (props) => {
    return (
        <div className="vehicles Preview">
            <Link to={`/Vehicles/${props.info.name}`}>
                <h3>{props.info.name}</h3>
                <img
                    src={`https://starwars-visualguide.com/assets/img/vehicles/${props.id}.jpg`}
                    alt={props.info.name + "image"}
                    onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                />
            </Link>
        </div>
    )
}

export default PreviewVehicles
