import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const StarShipList = (props) => {
    const [starShip, setStarShip] = useState({ "results": [] });
    useEffect(() => {
        const getStarShip = async () => {
            var url = props.starShip
            if (JSON.parse(localStorage.getItem('starShipsListStored')) === null) {
                const resp = await fetch(url);
                const data = await resp.json();
                const actualStarShip = {
                    [props.starShip]: data
                };
                const allData = Object.assign(actualStarShip, JSON.parse(localStorage.getItem('starShipsListStored')));
                setStarShip(data);
                localStorage.setItem('starShipsListStored', JSON.stringify(allData));
            } else {
                if (props.starShip in JSON.parse(localStorage.getItem('starShipsListStored')) === true) {
                    setStarShip(JSON.parse(localStorage.getItem('starShipsListStored'))[props.starShip]);
                    console.log("cached");
                } else {
                    const resp = await fetch(url);
                    const data = await resp.json();
                    const actualStarShip = {
                        [props.starShip]: data
                    };
                    const allData = Object.assign(actualStarShip, JSON.parse(localStorage.getItem('starShipsListStored')));
                    setStarShip(data);
                    localStorage.setItem('starShipsListStored', JSON.stringify(allData));
                }
            }
        }
        getStarShip();
    }, [props.starShip]);
    return (
        <div className="starShip">
            <Link to={`/Spaceships/${starShip.name}`}>
                <div className="starship mini">
                    <img
                        alt={starShip.name}
                        src={`https://starwars-visualguide.com/assets/img/starships/${props.starShip.split("/")[5]}.jpg`}
                        onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                    />
                </div>
            </Link>
        </div>
    )
}
export default StarShipList
