import React from 'react'
import FilmList from '../lists/filmList';
import CharacterList from '../lists/characterList'
import PlanetList from '../lists/planetList'
import {withRouter} from 'react-router-dom'

const Specie = (props) => {
    return (
        <div className="page">
            <button className="goBack" onClick={props.history.goBack}>Go back</button>            <div className="info">
                <img alt={props.info.name} key={props.info.name + "image"} src={`https://starwars-visualguide.com/assets/img/species/${props.info.url.split("/")[5]}.jpg`} />
                <div className="data">
                    <h2>{props.info.name}</h2>
                    <p>Classification: {props.info.classification}</p>
                    <p>Designation: {props.info.designation}</p>
                    <p>Average Height: {props.info.average_height}</p>
                    <p>Skin Colors: {props.info.skin_colors}</p>
                    <p>Hair Colors: {props.info.hair_colors}</p>
                    <p>Eye Colors: {props.info.eye_colors}</p>
                    <p>Average Lifespan: {props.info.average_lifespan}</p>
                    <p>Language: {props.info.language}</p>
                    <p>Homeworld: </p>
                    <PlanetList planet={props.info.homeworld} />
                </div>
            </div>
            <div className="related">
                <h1>Appears in</h1>
                <div className="filmsIn minis">
                    {props.info.films.map(film => {
                        return (
                            <FilmList key={`Species-film-${film.split("/")[5]}`} film={film} />
                        )
                    })}
                </div>
                <h1>People</h1>
                <div className="people minis">
                    {props.info.people.map(character => {
                        return (
                            <CharacterList key={`Species-people-${character.split("/")[5]}`} character={character} />
                        )
                    })}
                </div>
            </div>
        </div>
    )
}
export default withRouter(Specie)
