import React, { useEffect, useState } from 'react'
import PreviewSpecies from './previewSpecies'
import { Route } from 'react-router-dom'
import Specie from './specie'

const Species = () => {
    const [species, setSpecies] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getSpecies = async () => {
            if (localStorage.getItem('specieStored') === null) {
                let allData = [];
                for (let i = 1; i <= 4; i++) {
                    let url = `https://swapi.co/api/species?page=${i}`
                    const resp = await fetch(url);
                    const data = await resp.json();
                    allData = allData.concat(data.results);
                    setSpecies(allData);
                }
                localStorage.setItem('specieStored', JSON.stringify(allData));
            } else {
                console.log("cached")
                setSpecies(JSON.parse(localStorage.getItem('specieStored')));
            }
        }
        getSpecies();
    }, [pageNumber]);

    let specie = species.map((thisSpecie) => {
        return (
            <Route path={`/Species/${thisSpecie.name}`} key={`${thisSpecie.name}-Page`} render={() => <Specie info={thisSpecie} />} />
        )
    });

    const nextPage = () => {
        if (pageNumber < 4) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (pageNumber > 1) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Species">
                <div className="species">
                    {species.slice((pageNumber - 1) * 10, pageNumber * 10).map((specie) => {
                        return (
                            <PreviewSpecies
                                key={specie.name}
                                info={specie}
                                id={specie.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {specie}
        </div>
    )
}

export default Species
