import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const PlanetList = (props) => {
    const [planet, setPlanet] = useState({ "name": "n/a" });
    useEffect(() => {
        const getPlanet = async () => {
            if (JSON.parse(localStorage.getItem('planetListStored')) === null) {
                const resp = await fetch(props.planet);
                const data = await resp.json();
                const actualPlanet = {
                    [props.planet]: data
                };
                const allData = Object.assign(actualPlanet, JSON.parse(localStorage.getItem('planetListStored')));
                setPlanet(data);
                localStorage.setItem('planetListStored', JSON.stringify(allData));
            } else {
                if (props.planet in JSON.parse(localStorage.getItem('planetListStored')) === true) {
                    setPlanet(JSON.parse(localStorage.getItem('planetListStored'))[props.planet]);
                    console.log("cached");
                } else {
                    const resp = await fetch(props.planet);
                    const data = await resp.json();
                    const actualPlanet = {
                        [props.planet]: data
                    };
                    const allData = Object.assign(actualPlanet, JSON.parse(localStorage.getItem('planetListStored')));
                    setPlanet(data);
                    localStorage.setItem('planetListStored', JSON.stringify(allData));
                }
            }
        }
        getPlanet();
    }, [props.planet]);
    if (props.planet !== null) {
        return (
            <div className="planet">
                <Link to={`/Planets/${planet.name}`}>
                    <div className="planet mini">
                        <img
                            alt={planet.name}
                            src={`https://starwars-visualguide.com/assets/img/planets/${props.planet.split("/")[5]}.jpg`}
                            onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                        />
                    </div>
                </Link>
            </div>
        )
    } else {
        return (<p>unknown</p>)
    }
}
export default PlanetList
