import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const VehiclesList = (props) => {
    const [vehicles, setVehicles] = useState({ "results": [] });
    useEffect(() => {
        const getVehicles = async () => {
            var url = props.vehicles
            if (JSON.parse(localStorage.getItem('vehiclesListStored')) === null) {
                const resp = await fetch(url);
                const data = await resp.json();
                const actualVehicle = {
                    [props.vehicles]: data
                };
                const allData = Object.assign(actualVehicle, JSON.parse(localStorage.getItem('vehiclesListStored')));
                setVehicles(data);
                localStorage.setItem('vehiclesListStored', JSON.stringify(allData));
            } else {
                if (props.vehicles in JSON.parse(localStorage.getItem('vehiclesListStored')) === false) {
                    setVehicles(JSON.parse(localStorage.getItem('vehiclesListStored'))[props.vehicles]);
                    console.log("cached");
                } else {
                    const resp = await fetch(url);
                    const data = await resp.json();
                    const actualVehicle = {
                        [props.vehicles]: data
                    };
                    const allData = Object.assign(actualVehicle, JSON.parse(localStorage.getItem('vehiclesListStored')));
                    setVehicles(data);
                    localStorage.setItem('vehiclesListStored', JSON.stringify(allData));
                }
            }
        }
        getVehicles();
    }, [props.vehicles]);
    return (
        <div className="vehicles">
            <Link to={`/vehicles/${vehicles.name}`}>
                <div className="vehicles mini">
                    <img
                        alt={vehicles.name}
                        src={`https://starwars-visualguide.com/assets/img/vehicles/${props.vehicles.split("/")[5]}.jpg`}
                        onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                    />
                </div>
            </Link>
        </div>
    )
}
export default VehiclesList
