import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const FilmList = (props) => {
    const [film, setFilm] = useState({ "results": [] });
    const order = {
        1: 4, 2: 5, 3: 6, 4: 1, 5: 2, 6: 3, 7: 7
    };
    useEffect(() => {
        const getFilm = async () => {
            var url = props.film;
            if (JSON.parse(localStorage.getItem('filmListStored')) === null) {
                const resp = await fetch(url);
                const data = await resp.json();
                const filme = {
                    [props.film]: data
                };
                const allData = Object.assign(filme, JSON.parse(localStorage.getItem('filmListStored')));
                setFilm(data);
                localStorage.setItem('filmListStored', JSON.stringify(allData));
            } else {
                if (props.film in JSON.parse(localStorage.getItem('filmListStored'))) {
                    setFilm(JSON.parse(localStorage.getItem('filmListStored'))[props.film]);
                    console.log("cached");
                } else {
                    const resp = await fetch(props.film);
                    const data = await resp.json();
                    const filme = {
                        [props.film]: data
                    };
                    const allData = Object.assign(filme, JSON.parse(localStorage.getItem('filmListStored')));
                    setFilm(data);
                    localStorage.setItem('filmListStored', JSON.stringify(allData));
                }
            }
        }
        getFilm();
    }, [props.film]);
    return (
        <div className="film">
            <Link to={`/Films/${film.title}`}>
                <div className="mini">
                    <img alt={film.title} src={`https://starwars-visualguide.com/assets/img/films/${order[film.episode_id]}.jpg`} />
                </div>
            </Link>
        </div>
    )
}
export default FilmList
