import React from 'react'
import FilmList from '../lists/filmList';
import CharacterList from '../lists/characterList'
import { withRouter } from 'react-router-dom'

const Vehicle = (props) => {
    return (
        <div className="page">
            <button className="goBack" onClick={props.history.goBack}>Go back</button>            <div className="info">
                <img
                    alt={props.info.name}
                    key={props.info.name + "image"}
                    src={`https://starwars-visualguide.com/assets/img/vehicles/${props.info.url.split("/")[5]}.jpg`}
                    onError={(e) => { e.target.onerror = null; e.target.src = "https://as1.ftcdn.net/jpg/02/71/86/48/500_F_271864847_qVuzNqkJcnIAjkL9n5OHTsNaPTLqZbpG.jpg" }}
                />
                <div className="data">
                    <h2>{props.info.name}</h2>
                    <p>Model: {props.info.model}</p>
                    <p>Manufacturer: {props.info.manufacturer}</p>
                    <p>Cost In Credits: {props.info.cost_in_credits}</p>
                    <p>Length: {props.info.length}</p>
                    <p>Max Atmosphering Speed: {props.info.max_atmosphering_speed}</p>
                    <p>Crew: {props.info.crew}</p>
                    <p>Passengers: {props.info.passengers}</p>
                    <p>Cargo Capacity: {props.info.cargo_capacity}</p>
                    <p>Consumables: {props.info.consumables}</p>
                    <p>Vehicle Class: {props.info.Vehicle_class}</p>
                </div>
            </div>
            <div className="related">
                <h1>Appears in</h1>
                <div className="filmsIn minis">
                    {props.info.films.map(film => {
                        return (
                            <FilmList key={`startShip-film-${film.split("/")[5]}`} film={film} />
                        )
                    })}
                </div>
                <h1>Pilots</h1>
                <div className="pilots minis">
                    {props.info.pilots.map(character => {
                        return (
                            <CharacterList key={`startShip-pilot-${character.split("/")[5]}`} character={character} />
                        )
                    })}
                </div>
            </div>
        </div>
    )
}
export default withRouter(Vehicle)
